<template>
  <div class="containerWrap">
    <el-container class="container">
      <el-main class="rightMain">
        <el-container>
          <!-- <el-header class="dzy-main-header">
            <div class="btn-group-el">
              <el-button type="primary" icon="el-icon-plus" @click="add"
                >新增</el-button
              >
              <el-button type="success" icon="el-icon-view" @click="get"
                >查看</el-button
              >
              <el-button type="danger" icon="el-icon-delete" @click="del"
                >删除</el-button
              >
              <el-button type="warning" icon="el-icon-edit" @click="edit"
                >修改</el-button
              >
              <el-button type="primary" icon="el-icon-sort" @click="move"
                >党组织关系转移</el-button
              >
            </div>
            <div class="dzy-custom-search-box">
              <div>
                党员姓名 :
                <el-input
                  size="small"
                  placeholder="请输入姓名"
                  v-model="queryData.data.memberName"
                  clearable
                  class="dzy-custom-input"
                ></el-input>
              </div>
              <div>
                <el-button
                  size="small"
                  @click="searchHandleClick"
                  type="primary"
                  icon="el-icon-search"
                  >查询</el-button
                >
              </div>
            </div>
          </el-header> -->
          <el-main class="tableMain">
            <el-table
              ref="multipleTable"
              :data="tableData.records"
              style="width: 100%"
              :header-cell-style="{ backgroundColor: '#CF4B34', color: '#fff' }"
              :max-height="430"
              stripe
            >
              <el-table-column type="selection" width="55"></el-table-column>
              <el-table-column
                v-for="(item, index) in tableColumns"
                :key="index"
                :prop="item.prop"
                :width="item.width"
                :label="item.label"
              ></el-table-column>
            </el-table>
            <el-pagination
              background
              @current-change="handleCurrentChange"
              :current-page.sync="queryData.current"
              :page-size="queryData.size"
              layout="total, prev, pager, next, jumper"
              :total="tableData.total"
            ></el-pagination>
          </el-main>
        </el-container>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import { getSysLog } from "@/http/settingApi";
export default {
  data() {
    return {
      queryData: {
        current: 1,
        size: 10,
      },
      tableColumns: [
        {
          prop: "memberName",
          label: "姓名",
          width: "150px",
        },
        {
          prop: "typeCode",
          label: "党员状态",
          width: "",
        },
        // {
        //   prop: "cloudOrg.orgTelNumber",
        //   label: "党员编号",
        //   width: "200px",
        // },
        {
          prop: "modelCode",
          label: "模范类型",
          width: "",
        },
        {
          prop: "memberPhoneNumber",
          label: "联系方式",
          width: "",
        },
        {
          prop: "cloudOrg.orgAddress",
          label: "隶属组织",
          width: "",
        },
      ],
      tableData: [],
    };
  },
  mounted() {
    this.getTableData();
  },
  methods: {
    handleCurrentChange() {},
    //获取表格数据
    getTableData() {
      getSysLog(this.queryData).then((resp) => {
        let data = resp.value;
        if (data && data.code == "0000") {
          this.tableData = data.data;
        } else {
          this.$message.error(data.message);
        }
      });
    },
  },
};
</script>
<style scoped>
.containerWrap {
  padding: 10px;
  height: 100%;
  box-sizing: border-box;
}
.container {
  height: 100%;
}
</style>



