import req from '@/http/http'

//----------------字典类型-----------------------------------

//新增字典类型
export const addDictType = (data) => {
  let path = `/sys/dictType/add`
  return req.post(path, data)
}

//删除字典类型
export const delDictType = (data) => {
  let path = `/sys/dictType/delete`
  return req.post(path, data)
}

//查看字典类型详情
export const getDictType = (data) => {
  let path = `/sys/dictType/detail`
  return req.post(path, data)
}

//获取字典类型列表
export const getDictTypeList = (data) => {
  let path = `/sys/dictType/list`
  return req.post(path, data)
}

//修改字典类型
export const updateDictType = (data) => {
  let path = `/sys/dictType/update`
  return req.post(path, data)
}

//修改字典类型状态
export const updateDictTypeStatus = (data) => {
  let path = `/sys/dictType/updateStatus`
  return req.post(path, data)
}
//--------------字典值--------------------------------

